/* Reset básico */
* {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #f0f2f5;
  color: #2d2d2d;
  line-height: 1.6;
  padding: 2rem;
}

/* Cabeçalho */
header {
  background: linear-gradient(135deg, #0064cf, #00c6ff);
  color: #fff;
  text-align: center;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

header h1 {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

header p {
  margin: 0.3rem 0;
}

header a {
  color: #fff;
  text-decoration: underline;
  margin: 0 0.4rem;
}

/* Seções */
section {
  background-color: #fff;
  margin-top: 2rem;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

/* Títulos das seções */
section h2 {
  color: #007bff;
  margin-bottom: 1rem;
  border-bottom: 2px solid #007bff;
  padding-bottom: 0.5rem;
  font-size: 1.5rem;
}

/* Subtítulos */
section h3 {
  margin-top: 1rem;
  font-size: 1.2rem;
  color: #444;
}

/* Listas */
ul {
  margin-top: 0.5rem;
  padding-left: 1.5rem;
}

li {
  margin-bottom: 0.4rem;
}

/* Links externos */
a[target="_blank"] {
  font-weight: bold;
}

/* Responsivo */
@media (max-width: 600px) {
  body {
    padding: 1rem;
  }

  header h1 {
    font-size: 1.8rem;
  }

  section h2 {
    font-size: 1.3rem;
  }

  section h3 {
    font-size: 1.1rem;
  }
}
